import common from '../../common/common.js'

export default {
	data() {
		return {
			rechargeList:[],
			isall:0,
			page:0,
			type:'wallet',
			current:0,			//转出类型 0:不可提现账户 1:可提现账户
			amounts:0,			//转出金额
			popzhuan:false,		
			pingjun:0,			//平均每天单数
			needpingjum:10,		//需每天平均单数
			userInfo:[],
			
			info:{
				bg_color:'167,137,0',
				bg_alpha:1,
				delta:2,
				icon_color:'white'
			},				
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
		}
	},
	onLoad() {
		var that = this
		var userInfo = uni.getStorageSync('userInfo');
		var utype =  uni.getStorageSync('utype');			//用户类型
		
		that.userInfo = userInfo
		that.needpingjum = userInfo.shop_pingjundan
		
		
		that.getRechargeList();
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;




	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');
		that.page = 0;
		that.isall = 0;
		that.rechargeList = []

		that.getRechargeList();
		that.getUserInfo();
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	onReachBottom() {
		var that = this
		uni.showNavigationBarLoading();			
		
		that.getRechargeList();
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	methods: {
		toggleZhuan(){
			var that = this
			
			if(!that.popzhuan){
				var account = that.info.shop_account
				if(account == 0){
					uni.showToast({
						title:'余额不足',
						icon:'none',
						duration:1500
					})
					return false
				}
			}
			that.popzhuan = !that.popzhuan
		},
		checkAmounts(e){
			var that = this
			that.amounts = e.detail.value
			
		},
		submitSave(e){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			var wallet = userInfo.users_wallet
			var amounts = parseFloat(that.amounts)
			var weixin = e.detail.weixin
			
			if(amounts == 0){
				uni.showToast({
					title:'请输入提现金额',
					icon:'none',
					duration:1500
				})
				return false
			}
			
			if(amounts > wallet){
				uni.showToast({
					title:'余额不足',
					icon:'none',
					duration:1500
				})
				return false
			}
			
			uni.request({
				url: common.apiUrl + 'user/saveTixian',
				method: 'GET',
				data: {
					userid:userInfo.users_id,
					amount:amounts,
					weixin:weixin
				},
				success: res => {
					if(res.data.code == 0){
						uni.showToast({
							title:'操作成功',
							icon:'success',
							duration:1500,
							success:res=>{
								setTimeout(function(){
									that.resetAttr();
									that.getRechargeList();
									that.toggleZhuan();
									that.getUserInfo()
								})
							}
						})
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none',
							duration:1500									
						})
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		radioChange: function(evt) {
			var that = this
			that.current = evt.target.value;
			console.log(that.current);
		},
		//切换账户状态
		changeAccountType(e){
			var that = this
			var t_type = e.currentTarget.dataset.type
			if(t_type == that.type){
				return false
			}else{
				that.type = t_type
				that.resetAttr();
				that.getData();
			}
		},
		resetAttr(){
			var that = this
			that.page = 0
			that.isall = 0
			that.rechargeList = []
		},
		getData(){
			var that = this
			that.getRechargeList();
		},
		
		//获取用户信息
		getUserInfo(){
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
			var userid = userInfo.users_id
			if(userid > 0){
				uni.request({
					url: common.apiUrl+'user/getUserInfo',
					method: 'GET',
					data: {
						userid:userid
					},
					data: {
						userid:userid
					},
					success: res => {
						if(res.data.code == 0){
							var userInfo = res.data.userInfo;
							uni.setStorageSync('userInfo',userInfo);
							that.userInfo = userInfo
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}else{
				uni.showModal({
					title: '提示',
					content: '你还没登录，现在去登录吗？',
					showCancel: true,
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if(res.confirm){
							uni.navigateTo({
								url: '/pages/userlogin/userlogin',
								success: res => {},
								fail: () => {},
								complete: () => {}
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
		},
				
		//获取资金明细列表
		getRechargeList(){
			var that = this
			var isall = that.isall
			console.log('isall',isall)
			if(isall == 1){
				return false;
			}
			var userInfo = uni.getStorageSync('userInfo');
			var uid = userInfo.mem_id
			uni.request({
				url: common.apiUrl + 'user/getRechargeList',
				method: 'GET',
				data: {
					uid:userInfo.users_id,
					page:that.page+1
				},
				success: res => {
					that.rechargeList = that.rechargeList.concat(res.data.recharge_list);
					that.isall = res.data.isall;
					console.log(res.data.recharge_list);
					that.page = res.data.page
					console.log(that.page);
				},
				fail: () => {},
				complete: () => {}
			});
		}
		
	}
}
